import axios from "axios";
import React from "react";

export default function App() {
  const [albums, setAlbums] = React.useState([]);
  const [selectedAlbum, setSelectedAlbum] = React.useState(null);
  const [photos, setPhotos] = React.useState([]);

  const getAlbums = async () => {
    const response = await axios.get(
      "https://jsonplaceholder.typicode.com/albums"
    );
    setAlbums(response.data);
  };

  const getPhotos = async (albumId) => {
    const response = await axios.get(
      `https://jsonplaceholder.typicode.com/albums/${albumId}/photos`
    );
    setPhotos(response.data);
  };

  const onClickAlbum = (album) => {
    setSelectedAlbum(album);
    getPhotos(album.id).catch(console.error);
  };

  React.useEffect(() => {
    getAlbums().catch(console.error);
  }, []);

  return (
    <div>
      <h1>Albums</h1>
      {albums.map((album) => (
        <div
          onClick={() => onClickAlbum(album)}
          style={{
            display: "inline-block",
            padding: "0.5em",
            border: "1px solid black",
            margin: "0.25em",
            cursor: "pointer",
          }}
          key={album.id}
        >
          {album.title}
        </div>
      ))}
      <hr />
      <h1>Photos</h1>
      {!selectedAlbum && "Please select an album"}
      {selectedAlbum && <p>You selected album: {selectedAlbum.title}</p>}
      {selectedAlbum &&
        photos.map((photo) => (
          <div
            style={{
              display: "inline-block",
              padding: "0.5em",
              border: "1px solid black",
              margin: "0.25em",
            }}
            key={photo.id}
          >
            <p>{photo.title}</p>
            <img src={photo.thumbnailUrl} />
          </div>
        ))}
    </div>
  );
}
